import React, { useRef } from "react";
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import Box from "@mui/material/Box";
import { Button } from "@mui/material";
import ContentCopyIcon from '@mui/icons-material/ContentCopy';

export default function ComboBox() {
  const [inputValue, setInputValue] = React.useState("");
  const [cmd, setCMD] = React.useState("");
  const buttonRef = useRef(null);
  const buttonRef2 = useRef(null);
  var t = 'qqq'

  return (
    <>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "left",
          alignItems: "center"
        }}
      >
        <Autocomplete
          value={inputValue}
          disablePortal
          autoHighlight
          clearOnEscape
          onChange={(event, newValue) => {
            if (newValue) {
              buttonRef2.current.style.display = 'block'
              buttonRef.current.innerHTML = newValue.txt ;
              setCMD(newValue.txt)
            }
          }}
          onSelect={(event, newValue) => {
            setInputValue("");
          }}
          id="combo-box-demo"
          options={top100Films}
          sx={{ width: 400 }}
          renderInput={(params) => <TextField {...params} label="Hero" />}
        />
        <br />
        <div style={{ padding: 30 }} align="left" ref={buttonRef}>

        </div>
        
        <Button 
        ref = {buttonRef2}
        sx={{ display: 'none' ,
        fontSize: 35}}
        variant="contained"
        size="large"
        endIcon={<ContentCopyIcon />}
        onClick={() => {navigator.clipboard.writeText(cmd)}}>COPY</Button>
      </Box>
    </>
  );
}
